.orderSuccess{
    margin:auto;
    text-align:center;
    height:50vh;
    display:flex;
    flex-direction:column;
    align-items:center;
    justify-content:center;
    padding:10vmax;

}

.orderSuccess >svg{
    font-size: 7vmax;
    color:tomato;
}

.orderSuccess >p{
    font-size:2vmax;
}

.orderSuccess > a{
    background-color:black;
    color:white;
    margin:2vmax;
    cursor: pointer;
    font: 400 1vmax "Roboto";
    text-decoration:none;
    border:none;
    padding:1vmax 3vmax;
}

@media screen and (max-width:600px){
    
    .orderSuccess >a{
        padding:3vw 6vw;
        font :400 4vw "Roboto";
        margin:2vmax;
    }

    .orderSuccess>svg{
        font-size: 20vw;
    }

    .orderSuccess >p{
        margin:2vmax;
        font-size: 5vw;
    }

}