

.banner{
    background: rgb(63,94,251);
    background: radial-gradient(circle, rgb(44, 76, 237) 0%, rgb(217, 54, 86) 100%);
    height:100vmin;
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;
    justify-content: center;
    color:white;
    align-items: center;
    flex-wrap: wrap;
}

.banner > h1{
    margin: 5vmax;
    font: 600 2.5vmax "Oswald";
    text-shadow: 2px 1px 2px rgb(0, 0, 0);
}

.banner >p{
    font: 300 1.4vmax "Lucida Sans";
    text-shadow: 2px 1px 2px rgb(0, 0, 0);
}

.banner > a > button{
    margin-bottom: 5vmax;
    cursor: pointer;
    background-color: white;
    border: 1px solid white ;
    border-radius: 0;
    padding: 1vmax;
    transition:all 0.5s;
    width:9vmax;
    font:500 1vmax "Oswald";
}

.banner >a> button:hover{
    background-color: rgba(255, 255, 255, 0);
    color: white;
}

.banner::after{
    content:"";
    width:100vw;
    height: 100vmin;
    background-color: white;
    position: absolute;
    top: 0%;
    left: 0%;
    max-width: 100%;
    clip-path: polygon(100% 74%, 0% 100%, 100% 100%);

}

.homeHeading{
    text-align: center;
    font-family:"Oswald";
    font-size: 1.7vmax;
    border-bottom: 1px solid black;
    width:20vmax;
    padding: 1vmax;
    margin:5vmax auto;
    color:rgba(0, 0, 0, 0.693);
}


.container{
    display: flex;
    flex-wrap: wrap;
    margin:2vmax auto;
    width:80vw;
    justify-content:center;
    max-width:100%;
}


.productCard{
    width:14vmax;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    text-decoration: none;
    color:black;
    margin:2vmax;
    transition:all 0.5s;
    padding-bottom: 0.5vmax;
}

.productCard > img{
    width:14vmax;
}

.productCard > div{
    margin:0.5vmax;
   
    display: flex;
    justify-content: flex-start;
    align-items: center;
    
}
 
.productCardSpan{
    margin:3px;
    /* margin-top: 13px; */
    font: 300 0.7vmax "Oswald";
}

.productCard > p{
    font-family: "Oswald","Roboto", "Helvetica Ne";
    font-size: 1.3vmax;
    margin:1vmax 0.5vmax;
    margin-bottom: 0;
}

.productCard > span{
    margin:0.5vmax;
    color:tomato;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-size:  1vmax;
}

.productCard:hover{
    box-shadow: 0 0 5px black;
    transition:all 0.5s;
    transform: translateY(-1vmax);
}

@media screen and (max-width:600px) {
    .productCard > p{
        font-size: 1.7vmax;
    }

    .productCard >div{
        margin:0vmax;
        display:block;
    }

    .productCard >span{
        font-size: 1.5vmax;
    }

    .productCard >div>span{
        margin :0 0.5vmax;
        font: 300 1vmax "Oswald";
    }
}